<template>
    <div class="graficos-multilinea" id="graficos_multilinea">
        <svg class="svg-graficos-multilinea" id="svg_graficos_multilinea">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"    
    import * as d3Collection from 'd3-collection'
    
    export default {
        name: 'GraficosMultilinea',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            /* Tomando de: https://www.d3-graph-gallery.com/graph/line_several_group.html
            * Apoyado de: https://www.codenong.com/cs105121009/
            * https://stackoverflow.com/questions/50774487/d3-nest-not-a-function#62845802
            * http://bl.ocks.org/hubgit/raw/9133448/
            * https://www.geeksforgeeks.org/d3-js-nest-function/
            */
            var margin = {top: 10, right: 30, bottom: 30, left: 60},
                width = 460 - margin.left - margin.right,
                height = 400 - margin.top - margin.bottom;

            // append the svg object to the body of the page
            var svg = d3.select("#svg_graficos_multilinea")
            // .append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
            .append("g")
                .attr("transform",
                    "translate(" + margin.left + "," + margin.top + ")");

            //Read the data
            d3.csv("https://raw.githubusercontent.com/holtzy/data_to_viz/master/Example_dataset/5_OneCatSevNumOrdered.csv").then(data => {                

                // console.log(data);
                // group the data: I want to draw one line per group
                var sumstat = d3Collection.nest() // nest function allows to group the calculation per level of a factor
                    .key(function(d) { return d.name;})
                    .entries(data);

                // console.log(sumstat);

                // Add X axis --> it is a date format
                var x = d3.scaleLinear()
                    .domain(d3.extent(data, function(d) { return d.year; }))
                    .range([ 0, width ]);
                svg.append("g")
                    .attr("transform", "translate(0," + height + ")")
                    .call(d3.axisBottom(x).ticks(5));

                // Add Y axis
                var y = d3.scaleLinear()
                    .domain([0, d3.max(data, function(d) { return +d.n; })])
                    .range([ height, 0 ]);
                svg.append("g")
                    .call(d3.axisLeft(y));

                // color palette
                var res = sumstat.map(function(d){ return d.key }) // list of group names
                // console.log(res);
                var color = d3.scaleOrdinal()
                    .domain(res)
                    .range(['#e41a1c','#377eb8','#4daf4a','#984ea3','#ff7f00','#ffff33','#a65628','#f781bf','#999999'])

                // Draw the line
                svg.selectAll(".line")
                    .data(sumstat)
                    .enter()
                    .append("path")
                        .attr("fill", "none")
                        .attr("stroke", function(d){ return color(d.key) })
                        .attr("stroke-width", 1.5)
                        .attr("d", function(d){
                            return d3.line().x(function(d) { return x(d.year); }).y(function(d) { return y(+d.n); })(d.values)
                        })

            })
        }
    }
</script>

