<template>
    <div class="basic-line" id="basic_line">
        <svg class="my-dataviz" id="my_dataviz"></svg>
    </div>
</template>

<script>
import * as d3 from 'd3'

export default {
    name: 'BasicLine',
    mounted(){
        // set the dimensions and margins of the graph
        var margin = {top: 10, right: 30, bottom: 50, left: 60},
            width = 460 - margin.left - margin.right,
            height = 400 - margin.top - margin.bottom;

        // append the svg object to the body of the page
        var svg = d3.select("#my_dataviz")
        // .append("svg")
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)
        .append("g")            
            .attr("transform",
                    "translate(" + margin.left + "," + margin.top + ")");

        // Establecemos las escalas y sus rangos a lo largo de los ejes x y
        // Add X axis --> it is a date format
        var x = d3.scaleTime().range([0, width]);
        var y = d3.scaleLinear().range([height, 0]);   
        // Creamos una línea que más tarde cargaremos en el path
        var valueLine = d3.line()
            .x(function(d) { return x(d.date); })
            .y(function(d) { return y(d.value); });

        var parseTime = d3.timeParse("%Y-%m-%d");

        // Obtenemos los datos
        // var csvUrl = "https://raw.githubusercontent.com/holtzy/data_to_viz/master/Example_dataset/3_TwoNumOrdered_comma.csv";
        var csvUrl = "data3.csv";

        // //Read the data
        d3.csv(csvUrl).then(data => {      

            // console.log(data);
            
            data.forEach(function(d) {
                console.log(typeof(d.date));
                // Parse a dia por mes
                d.date = parseTime(d.date);                    
                // Parse a valor numérico
                d.value = +d.value; 
                // console.log(d.date);
                // console.log(typeof(d.date));     
            
            })

            // Escalamos el rango de los datos
            // x.domain(d3.extent(data, function(d) { console.log(d.date);return d.date; }));
            x.domain(d3.extent(data, function(d) { return d.date; }));
            y.domain([0, d3.max(data, function(d) { return d.value; })]);   

            // Añadimos los ejes
            svg.append("g")
                .attr("transform", "translate(0," + height + ")")
                .call(d3.axisBottom(x).tickFormat(d3.timeFormat("%Y-%m-%d")))
                .selectAll("text")
                .style("text-anchor", "end")
                .attr("dx", "-.8em")
                .attr("dy", ".15em")
                .attr("transform", function() {
                    return "rotate(-40)"
                    });
            // Add Y axis
            svg.append("g")
                .call(d3.axisLeft(y));

            // Add the line
            svg.append("path")
                .datum(data)
                .attr("fill", "none")
                .attr("stroke", "steelblue")
                .attr("stroke-width", 1.5)        
                .attr("d", valueLine);
            
        });

    } // end mounted
}
</script>