<template>
    <div class="graficos-lineas" id="graficos_lineas">
        <svg class="svg-graficos-lineas" id="svg_graficos_lineas">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'GraficosLineas',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            /* Tomando de: https://d3-spanish.readthedocs.io/es/latest/ejemplos/graficos/line_chart.html
            */

            // Establecemos las dimensiones y los margenes del grafico
            var margin = {top: 30, right: 20, bottom: 50, left: 30},
                width = 680 - margin.left - margin.right,
                height = 345 - margin.top - margin.bottom;

            // Establecemos las escalas y sus rangos a lo largo de los ejes x y
            var x = d3.scaleTime().range([0, width]);
            var y = d3.scaleLinear().range([height, 0]);    

            // Creamos la figura svg
            var svg = d3.select("#svg_graficos_lineas")
                        .attr("width", width + margin.left + margin.right)
                        .attr("height", height + margin.top + margin.bottom)
                        .append("g")
                        .attr("transform",
                            "translate(" + margin.left + "," + margin.top + ")");

            // Creamos una línea que más tarde cargaremos en el path
            var valueLine = d3.line()
                            .x(function(d) { return x(d.date); })
                            .y(function(d) { return y(d.close); });

            
            
            // Compilamos un parser para las fechas (por ejemplo: 2-May-12)
            var parseTime = d3.timeParse("%d-%b-%y");

            // Creamos el eje X, formateando las fechas
            var xAxis = d3.axisBottom(x).tickFormat(d3.timeFormat("%Y-%m-%d"));

            // Obtenemos los datos
            var csvUrl = "https://gist.githubusercontent.com/d3noob/402dd382a51a4f6eea487f9a35566de0/raw/6369502941b44261f381399a24fb455cb4290be8/data.csv";
            
            d3.csv(csvUrl).then(data => {               
                // console.log(data);
                // console.log(data[0].date);

                // Los formateamos
                data.forEach(function(d) {
                    // Parse a dia por mes
                    d.date = parseTime(d.date);                    
                    // Parse a valor numérico
                    d.close = +d.close;                    
                });

                // Escalamos el rango de los datos
                x.domain(d3.extent(data, function(d) { return d.date; }));
                y.domain([0, d3.max(data, function(d) { return d.close; })]);                

                // Añadimos y estilizamos la línea
                svg.append("path")
                    .data([data])
                    .attr("class", "line")
                    .attr("d", valueLine);

                // Añadimos los ejes
                svg.append("g")
                    .attr("transform", "translate(0," + height + ")")
                    .call(xAxis)
                    .selectAll("text")
                    .style("text-anchor", "end")
                    .attr("dx", "-.8em")
                    .attr("dy", ".15em")
                    .attr("transform", function() {
                        return "rotate(-40)"
                        });

                svg.append("g")
                    .call(d3.axisLeft(y));
            });
            
        }
    }
</script>

<style>
    .line{
        fill: none;
        stroke: steelblue;
        stroke-width: 2px;
    }

</style>